<template>
  <div class="hs-container">
    <c-header ref="CHeader" isBackNav :title="$t('base.EditFrequentContact')" fixedBackPath="/personal/frequentContacts">
      <div slot="right" class="header-right" @click="_save">
        <p>{{$t('base.Save')}}</p>
      </div>
    </c-header>
    <hs-section>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell-name">
          <p>{{$t('base.Name')}}</p>
          <p>{{details.Name}}</p>
        </div>
      </hs-cell-1>
      <hs-cell-2 :title="$t('base.CellphoneNumber')" v-model="details.Mobile" :edit="true" :placeholder="`(${$t('base.RequiredFill')}) ${$t('base.PleaseFill')}`"></hs-cell-2>
      <hs-cell-2 :title="$t('base.Email')" v-model="details.Email" :edit="true" :placeholder="`(${$t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"></hs-cell-2>
    </hs-section>
    <hs-section>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell-default">
          <p>{{$t('base.SetAsDefaultContact')}}</p>
          <hs-switch v-model="details.IsDefault" @input="_setDefaultContact"></hs-switch>
        </div>
      </hs-cell-1>
    </hs-section>
    <hs-section v-if="!details.IsCurrentLoginUser">
      <hs-cell-1 :showLogo="false" @click.native="showAlert = true">
        <div class="custom-cell-delete">
          <p>{{$t('base.RemoveThisFrequentContact')}}</p>
        </div>
      </hs-cell-1>
    </hs-section>
    <hs-alert :show.sync="showAlert" :content="$t('base.WhetherToConfirmTheDeletionOfThisFavoriteContact')" @confirm="_remove"></hs-alert>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCell2 from 'base/hs-tableview/hs-cell-2/hs-cell-2'
import HsSwitch from 'base/hs-switch/hs-switch'
import HsAlert from 'base/hs-alert/hs-alert'
import {kpiUserSetDefaultContact, kpiUserDeleteDefaultContact} from 'api'
export default {
  created() {
    this.details = JSON.parse(this.$route.query.details)
    // console.log('this.details: ', this.details)
  },
  data() {
    return {
      details: {},
      showAlert: false
    }
  },
  methods: {
    _setDefaultContact() {
      let params = {
        ID: this.details.ID,
        IsDefault: this.details.IsDefault,
        Name: this.details.Name,
        Mobile: this.details.Mobile,
        Email: this.details.Email
      }
      kpiUserSetDefaultContact(params).then((res) => {
        console.log('设置常用联系人: ', res)
      })
    },
    _remove() {
      let params = {
        id: this.details.ID
      }
      kpiUserDeleteDefaultContact(params).then((res) => {
        console.log('删除常用联系人: ', res)
        if (res.ResultData) {
          this.$router.go(-1)
        } else {
          this.$toast(this.$t('TM.RemovalFailedPleaseTryAgainLater'))
        }
      })
    },
    _save() {
      // trim处理
      this.details = this.handleOfTrim({...this.details})
      if (this.noValue(this.details.Name)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Name')))
        return
      } else if (this.noValue(this.details.Mobile)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.CellphoneNumber')))
        return
      } else if (!this.isPhoneNumber(this.details.Mobile)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.CellphoneNumber')))
        return
      } else if (!this.noValue(this.details.Email) && !this.isEmail(this.details.Email)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.Email')))
        return
      }
      let params = {
        ID: this.details.ID,
        IsDefault: this.details.IsDefault,
        Name: this.details.Name,
        Mobile: this.details.Mobile,
        Email: this.details.Email
      }
      kpiUserSetDefaultContact(params).then((res) => {
        this.$refs.CHeader && this.$refs.CHeader.backToFixedPath()
      })
    }
  },
  components: {
    CHeader,
    HsSection,
    HsCell1,
    HsCell2,
    HsSwitch,
    HsAlert
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .custom-cell-name
    @extend .flex-row
    @extend .flex-between
    width 100%
    >p
      &:nth-child(2)
        font-weight bold
  .custom-cell-default
    @extend .flex-row
    @extend .flex-between
    width 100%
  .custom-cell-delete
    color $color_theme
</style>



